.footer{
    background-color: #f7f7f7;
    margin-top: 200px;

}

.footer-wrapper {
    border-top: 1px solid #e7e7e7;
    border-bottom: 1px solid #e7e7e7;
    margin-top: 200px;

}

.footer .footer-nav-wrapper {
    margin: 0 auto;
    width: 1180px;
    padding: 20px 0px;
}

.footer .footer-item-wrapper{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 20px;
}

.footer .footer-item-wrapper .footer-item{
    font-size: 13px;
    color: #4a4a4a;
}

.footer .footer-item-wrapper .footer-info-item{
    display: flex;
    gap: 10px;
    flex-direction: column;
}

.footer .footer-item-wrapper .footer-info-item .info-title{
    font-size: 16px;
    font-weight: 700;
    color: #686868;
}

.footer .footer-item-wrapper .footer-info-item .info-description{
    font-size: 13px;
    font-weight: 400;
    color: #939393;
    line-height: 19px;
}